<template>
 <main class="container-fluid">
   <div class="row">
     <div class="map" :style = '[ data.length >1 ? {backgroundImage:`url(${pic})`} :{backgroundImage:none}]'>
       <svg></svg>
       <div class="mapInfo"  v-if="data.length>1">
         <h4 >Weather in {{cityName}}</h4>
         <p class="mb-1">今日氣象</p>
         <p class="weatherInfo mb-1">天氣：{{filterName[0].weather[0].description}} <img :src="`http://openweathermap.org/img/w/${filterName[0].weather[0].icon}.png`" alt=""></p>
         <p class="weatherInfo mb-1">最高溫：{{filterName[0].main.temp_max}}</p>
         <p class="weatherInfo mb-1">最低溫：{{filterName[0].main.temp_min}}</p>
         <p class="weatherInfo mb-1">平均溫：{{filterName[0].main.temp}}</p>
         <p class="weatherInfo mb-1">風速：{{filterName[0].wind.speed}}</p>
         <p class="weatherInfo mb-1"></p>
       </div>
     </div>
    <div>
    </div>
   </div>
   <section class="container-fluid">
     <div class="row">
       
     </div>
   </section>
 </main>
 
</template>

<script>
var taiwan =[
{
  name:'台灣',
  engname:'Taiwan',
  pic:'https://www.articlelike.com/timthumb/index.php?src=https://www.articlelike.com/manage/0/product/18907/1477531184_534.jpg'
},{
  name:'基隆市',
  engname:"Keelung",
  pic:'https://cdn2.ettoday.net/images/2658/d2658598.jpg'
},{
  name:'新北市',
  engname:"Taipei",
  pic:'http://2.bp.blogspot.com/-3KIZPCuhFXY/UhSSbFRt3OI/AAAAAAAALDI/R2-luUG5a_k/s1600/DSC_9749_2.jpg',
},{
  name:'臺北市',
  engname:"Taipei",
  pic:'https://images.pexels.com/photos/260566/pexels-photo-260566.jpeg?auto=compress&cs=tinysrgb&h=350'
},{
  name:'桃園市',
  engname:"Taoyuan",
  pic:'https://s1.yimg.com/uu/api/res/1.2/ikiVxFSULCkdwsCSTXYIJg--/YXBwaWQ9eXRhY2h5b247cT03NTs-/http://media.zenfs.com/en/homerun/feed_manager_auto_publish_494/98d64103740018b938649e7f6eb02df1'
},{
  name:'新竹縣',
  engname:"Hsinchu",
  pic:'https://www.bomb01.com/upload/news/original/9aa24b5e3df95e7e39764874cbb83a2c.jpg'
},{
  name:'新竹市',
  engname:"Hsinchu",
  pic:'https://travelimg.yam.com/DATA/ARTICLE/20150731173117358.jpg'
},{
  name:'臺中市',
  engname:"Taichung",
  pic:'http://3.bp.blogspot.com/-Z97JrD_r7lA/Uho8gsuj-PI/AAAAAAAAAh4/Ns2gXdDfe50/s1600/_MG_0613B.jpg'
},{
  name:'臺南市',
  engname:"Tainan",
  pic:'https://img.banbi.tw/20180815134218_24.jpg'
},{
  name:'高雄市',
  engname:"Kaohsiung",
  pic:'https://khh.travel/FileAttPic.ashx?l=1&id=142&w=2048&h=2048'
},{
  name:'苗栗縣',
  engname:"Miaoli",
  pic:'http://cdn.peoplenews.tw/news/67bd1deb-70fa-4c16-8b94-068b38954120.jpg'
},{
  name:'南投縣',
  engname:"Nantou",
  pic:'https://atlbusinessjournal.com/wp-content/uploads/2016/04/unnamed-file.jpg'
},{
  name:'雲林縣',
  engname:"Yunlin",
  pic:'https://2.bp.blogspot.com/-gIfvdUmMZhk/Wlv4IdANfQI/AAAAAAAAYuc/61badeiS45gkkRZNnP1RUUQsegd0wNEjwCLcBGAs/s1600/10.jpg'
},{
  name:'宜蘭縣',
  engname:"Yilan",
  pic:'https://farm6.staticflickr.com/5460/17802300550_ef3e4f80a3_b.jpg'
},{
  name:'屏東縣',
  engname:"Pingtung",
  pic:'http://pic.pimg.tw/djphoto/1372257602-4111062589.jpg'
},{
  name:'臺東縣',
  engname:"Taitung",
  pic:'https://pgw.udn.com.tw/gw/photo.php?u=https://uc.udn.com.tw/photo/2017/07/07/liontravel/3729454.jpg&x=0&y=0&sw=0&sh=0&exp=3600'
},{
  name:'花蓮縣',
  engname:"Hualien",
  pic:'https://blog.hotelscombined.com.tw/wp-content/uploads/2017/07/Beautiful-Cliff-in-Hualien-Taiwan-444281545.jpg'
},{
