  engname:"Penghu",
  pic:'https://www.taiwan.net.tw/att/1/big_scenic_spots/pic_653_7.jpg'
},{
  name:'嘉義縣',
  engname:"Jiayi Shi",
  pic:'https://pic.pimg.tw/dosn02/1438063959-1027424140.jpg'
},{
  name:'嘉義市',
  engname:"Jiayi Shi",
  pic:'http://farm9.staticflickr.com/8333/8414685382_810d6daa36_b.jpg'
},{
  name:'彰化縣',
  engname:"Chang-hua",
  pic:'https://pic.pimg.tw/car0126/1526664097-3738108602.jpg?v=1526664128'
},{
  name:'金門縣',
  engname:'Xiguoshan',
  pic:'https://kinmen.travel/image/10363/1024x768'
}
]
export default {
  name: 'HelloWorld',
  data () {
    return {
      cityName:'' || '台灣',
      data:[],
      taiwan:taiwan,
      pic:'',
    }
  },
  computed:{
    filterName(){
      const vm = this;
      let city = [];
      let cityName = vm.taiwan.filter((item)=>{
        if(item.name == vm.cityName){
          vm.pic = item.pic
          return item;
        }
       })
      city = vm.data.filter((item)=>{
        if(cityName[0].engname == item.name){
          return item;
        }
      })
    
      

      console.log('vm,pic',vm.pic)
      console.log(vm.data);
      console.log(city)
      return city
    }
  },
  methods:{
    getMap(){
        let vm =this;
        var projection = d3.geo.mercator().center([121, 24]).scale(8000);
        var path = d3.geo.path().projection(projection);

        d3.json("@/../static/taiwan.json", function(error, topology) {
            console.log(topology);
            console.log(error)
            // 縣市/行政區界線
            d3.select("svg").append("path").datum(
                topojson.mesh(topology,
                    topology.objects["County_MOI_1070516"],
                    function(a,b) {
                        return a !== b;
                    })).attr("class", "subunit-boundary");
            d3.select("svg").selectAll("path")
                .data(topojson.feature(topology, topology.objects.County_MOI_1070516).features)
                .enter()
                .append("path")
                .attr({
                    d: path,
                    "name": function(d) {
                        return d.properties["COUNTYNAME"];
                    },
                    fill: 'rgba(133,213,247, 0.8)',
                    stroke: "rgba(255, 255, 255, 0.5)",
                    'stroke-width': "2"
                })
                .on("mouseenter", function(d) {
                    d3.select(this).attr({
                      "fill":"rgba(133,213,247, 0.5)",
                      })
                      vm.cityName = d.properties.COUNTYNAME
                      console.log(d.properties.COUNTYNAME);
                }).on("mouseleave", function(d) {
                    d3.select(this).attr({
                      "fill": "rgba(133,213,247, 0.8)",
                   })
                });
        });
    },
    getData(){
      const vm =this;
      this.axios.get('https://api.openweathermap.org/data/2.5/group?id=1668284,6724654,7280290,1675151,1668399,1548565,7280289,1674197,1667905,1671971,1671564,1665194,1668352,1670479,1668295,1674502,1670651,1675247,1678836,1679136&lang=zh_TW&units=metric&APPID=fe5145f9c52ca061eec3a0ce448c8864')
      .then((res)=>{
        console.log(res.data);
        vm.data = [...res.data.list];
        console.log('vm.data',vm.data)
      })
      .then(()=>{
        console.log('aaa')
      })
      .catch((err)=>{
        console.log(err);
      })
    }
  },
  created(){
   
  },
  mounted(){
    this.getData();
    this.getMap();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import url('https://fonts.googleapis.com/css?family=Roboto:500');
main{
  position: relative;
  min-height:100vh ;
  /* background-image: linear-gradient(to top, #9890e3 0%, #b1f4cf 100%); */
  background: url('~@/assets/images/bg-kv2.jpg') center;
  background-size: cover;
  z-index: 1;
  &::after{
    content:'';
    display: block;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 2;
    background: #000;
    right: 0;
    top:0;
    background: radial-gradient(ellipse at center, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 95%,rgba(255,255,255,0) 100%);
  }
  .mapInfo{
    letter-spacing: 1.8px;
    position: absolute;
    right: -25%;
    top: 20%;
    font-size: 23px;
    h4{
      color: #2a5cb0;
      font-family: 'Roboto', sans-serif;
      font-weight: 500;
      text-shadow: 0 0 0.2em #af95d9;
    }
    p{
      text-align: center;
    }
    .weatherInfo{
      font-size: 20px
    }
  }
}
.map{
  background: linear-gradient(45deg, rgba(255,241,235,0.69) 0%,rgba(236,229,234,0.88) 10%,rgba(216,215,232,0.89) 21%,rgba(205,219,242,0.95) 62%,rgba(213,235,251,0.98) 83%,rgba(181,255,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  border-radius: 10px 10px 10px 10px;
  width: 800px;
  height: 600px;
  margin: 10% auto 0 auto;
  position: relative;
  z-index: 100;
  box-shadow: inset 1px -2px 9px 1px #2a6fb2, -4px 7px 16px 2px #0a2b358c, -9px 6px 16px 2px #5ea4e3d9;
  background-size: cover;
}
svg{
 position: relative;
 z-index: 99;
 width: 100%;
 height: 100%;
}

</style>



